   height: 80%;
   width: 80%;
   margin: 50px auto;
}
.story .title {
   position: relative;
   margin: 0 auto;
   width: 75%;
   height: 30px;
   border-radius: 15px;
   background: orange;
}
.story .title .story-text {
   width: 100%;
   height: 100%;
   font-size: 20px;
   line-height: 30px;
   text-align: center;
   font-weight: bold;
}
.story .title .return {
   position: absolute;
   top: 3px;
   right: 10px;
   height: 24px;
   width: 42px;
   opacity: 0.6;
}
.story .title .return:hover{
   cursor: pointer;
   opacity: 1;
}
.story .title .return::before {
   content: "";
   position: absolute;
   height: 0;
   width: 0;
   left: 0;
   top: 0;
   border: 12px solid transparent;
   border-right-color: #ccc;
}
.story .title .return::after {
   content: "";
   position: absolute;
   right: 0;
   top: 6px;
   width: 18px;
   height: 12px;
   background: #ccc;
}
.story .column{
   width: 75%;
   margin: 30px auto;
   display: -webkit-box;
   display: -ms-flexbox;
   display: flex;
   -ms-flex-pack: distribute;
       justify-content: space-around;
}
.story .column .column-item{
   position: relative;
   width: 50px;
   height: 50px;
   background: orange;
   -webkit-transition: all 1.5s ease;
   transition: all 1.5s ease;
   font-size: 20px;
   line-height: 50px;
   text-align: center;
}
.story .column .column-item:before {
   content: '';
   position: absolute;
   top: -31px;
   left: 23px;
   width: 0px;
   height: 55px;
   border: 2px solid orange;
   background: orange;
   -webkit-transition: all 1.5s ease;
   transition: all 1.5s ease;
   z-index: -1;
}
.story .column .column-item.active{
   -webkit-transform: translateY(20px);
           transform: translateY(20px);
}
.story .column .column-item.active::before{
   height: 75px;
   top: -51px;
}
.story .column .column-item:hover {
   background: rgb(204, 124, 20);
}
.story .column .column-item:hover:before{
   background: rgb(204, 124, 20);
   border-color: rgb(204, 124, 20);
}
.picBox {
   padding: 20px 50px;
   margin: 50px auto;
   height: 430px;
