.picBox img {
   height: 200px;
   width: 280px;
   border-radius: 5px;
   margin: 20px;
}
.picBox img.fadein {
   -webkit-animation: fadein .5s ease;
           animation: fadein .5s ease;
}
.picBox .expression {
   font-size: 20px;
   color: #088;
   text-align: left;
   font-weight: bolder;
